<template>
  <div class='item-cell-wrapper'>
    <Row>
      <Col :span='8'>
      <img :src="picurl" class="item-cell-img">
      </Col>
      <Col :span='16'>
	      <div class="item-cell-title item-cell-text">{{title}}</div>
	      <div class="item-cell-sku item-cell-text">
	      	{{sku||'暂无规格'}}
	  	  </div>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  props: ['picurl', 'itemsku','title'],
  data() {
    return {}
  },
  computed: {
    sku() {
      if (!this.itemsku) {
        return ''
      }
      let obj;
      try{
       obj = JSON.parse(this.itemsku);
      }catch(e) {
        obj = {}
      }
      let res = '';
      Object.keys(obj).forEach(it => {
        res += '/ '+obj[it] + ' '
      })
      res = res.slice(1)
      return res
    }
  }
}

</script>
<style>
.item-cell-wrapper {
  position: relative;
  width: 100%;
  height: 100px;
  min-width: 300px
}
.item-cell-img {
	width: 80px;
	height: 80px;
	display: block;
	margin:5px 0;

}
.item-cell-title {
	font-weight: bold;
	height: 40px;
	line-height: 40px;
	width: 100%;
}
.item-cell-sku {
  color: #999
}
</style>



