<template>
  <div style="margin:10px">
    <Button type="primary" style="margin-bottom:10px;" @click='backHome'>返回上一级</Button>
    <!-- <div class="order-sec-wrapper">
      <div class='sec-header'>订单信息</div>
      <div class="sec-main" v-if='orderData'>
        <div class="order-status">{{statusMap[orderData.status]}}</div>
        <div v-for='(item,index) in orderinfo' class="order-info-detail">
          <span>{{item.label}}:</span><span>{{orderData[item.field]}}</span>
        </div>
        <div class='order-info-detail order-remark'>
          <span>客户备注:</span><span>{{orderData.remark||'无'}}</span>
        </div>
      </div>
    </div> -->
    <div class="order-sec-wrapper">
     <div class='sec-header' style="position:relative;">
        <span>发货信息</span>
        <Button style="position: absolute;right: 0;top:0;height: 100%;width: 100px;" type="primary" @click="lookMemberDetail">查看会员信息</Button>
      </div> 
      <div class="sec-main" v-if='!!Object.keys(addressData).length'>
        <div class="order-mem-avatar">
          <Avatar style="background-color: #3bc0c3" icon="person"/>
          <span class="mem-name">{{'收货人姓名：'}}{{addressData?addressData.realname:''}}</span>
        </div>
        <div class='order-info-detail'>
          <span>订单状态:</span><span>{{statusMap[orderData.paystatus]}}</span>
        </div>
        <div class='order-info-detail'>
          <span>联系电话:</span><span>{{addressData?addressData.telephone:''}}</span>
        </div>
        <div class='order-info-detail'>
          <span>收货地址:</span><span>{{addressData?`${addressData.province} - ${addressData.city} - ${addressData.district} - ${addressData.address}`:''}}</span>
        </div>
      </div>
      <div class="sec-main" v-else>
        未填写收货信息
      </div>
    </div>
    <!-- <div class="order-sec-wrapper">
      <div class='sec-header'>消费信息</div>
      <div class="sec-main">
        <Table border ref="selection" :columns="itemcolumns" :data="tableData"></Table>
        <div class="order-sum">
          <div class="sum-item"><span>支付方式：</span><span>{{payTypeObj[orderData.ordertype]}}</span></div>
          <div class="sum-item"><span>已支付：</span><span>{{!!toPay?orderData.amount:0}}</span></div>
          <div class="sum-item"><span>待支付：</span><span>{{!!toPay?0:orderData.amount}}</span></div>
          
        </div>
      </div>
    </div> -->
    <div class="order-sec-wrapper">
      <div class='sec-header'>处理订单</div>
      <div class='sec-main'>
        <Button type='primary' style='width:100px;' @click='handleDelivery'
          :disabled='orderData.paystatus!=1'>发货
        </Button>
      </div>
    </div>

    <Modal v-model='showDelivery' @on-visible-change='handleModalChange'>
      <div slot='header' style='color:#3bc0c3'>
        <Icon type="information-circled"></Icon>
        <span>确认发货</span>
      </div>
      <div>
        <span>请输入物流单号：</span> <Input style='width:300px;' v-model='logisticsnum'></Input>
      </div>
      <div slot='footer'>
        <Button type='primary' :loading='deliveryLoading' @click='handleConfirmDeliv'>确认发货</Button>
        <Button type='ghost' @click='showDelivery=false'>取消</Button>
      </div>
    </Modal>
  </div>
  </div>
</template>
<script>
import itemCell from "./item-table-cell.vue";
export default {
  data() {
    return {
      showDelivery: false,
      logisticsnum: "",
      deliveryLoading: false,
      orderid: this.$route.query.orderid,
      orderData: {},
      orderinfo: [
        {
          field: "orderid",
          label: "订单编号"
        },
        {
          field: "createtime",
          label: "下单时间"
        },
        {
          field: "updatetime",
          label: "更新时间"
        }
      ],
      payTypeObj: {
        "1": "微信",
        "2": "积分",
        "3": "钱包"
      },
      addressInfo: [
        {
          field: "phone",
          label: "联系电话"
        },
        {
          field: "memlevel",
          label: "会员等级"
        }
      ],
      appointInfo: [
        {
          field: "mobile",
          label: "联系电话"
        },
        {
          field: "memlevel",
          label: "会员等级"
        }
      ],
      statusMap: {
        "0": "未支付",
        "1": "待发货",
        "2": "已发货",
        "3": "待评价",
        "5": "已取消",
        "4": "已完成"
      },
      tableData: [],
      addressData: {},
      itemcolumns: [
        {
          title: "商品",
          key: "title",
          minWidth: 400,
          render(h, params) {
            return h(itemCell, {
              props: {
                picurl: params.row.itempic,
                itemsku: params.row.itemsku,
                title: params.row.title
              }
            });
          }
        },
        {
          title: "单价(元)",
          key: "price",
          width: 100,
          align: "center"
        },
        {
          title: "数量",
          key: "num",
          width: 100,
          align: "center"
        },
        {
          title: "小计(元)",
          key: "count",
          align: "center",
          width: 150,
          render: (h, params) => {
            let c = +params.row.price * +params.row.num;
            return h("span", c);
          }
        }
      ]
    };
  },
  computed: {
    toPay() {
      return this.orderData.status == 0 || this.orderData.status == 5 ? 0 : 1;
    }
  },
  components: {
    "item-cell": itemCell
  },
  mounted() {
    this.$sitemap.prvname = "订单管理";
    this.$sitemap.currname = "订单详情";
    this.getOrderDetail(this.orderid);
  },
  methods: {
    backHome() {
      this.$router.go(-1);
    },
    //获取订单详情
    getOrderDetail(orderid) {
      this.$http
        .post(this.$api.order_detail, { orderid })
        .then(res => {
          let resData = res.data.data;
          console.log('resData',resData)
          if (res.data.result == "1" && resData) {
            console.log(resData);
            this.orderData = resData || {};
            this.addressData = resData|| {};
            this.tableData = resData|| [];
          }
        })
        .catch(erro => {});
    },
    //发货的弹框
    handleModalChange(status) {
      if (!status) {
        this.logisticsnum = "";
      }
    },

    //点击发货
    handleDelivery() {
      this.showDelivery = true;
    },

    //点击确认发货
    handleConfirmDeliv() {
      let lognum;
      lognum = this.logisticsnum.trim();
      if (!lognum) {
        this.$Message.error("请输入物流单号！");
        return;
      }
      this.deliveryLoading = true;
      this.addLogistic(this.orderid, lognum)
        .then(() => {
          this.deliveryLoading = false;
          this.showDelivery = false;
        })
        .catch(error => {
          this.$Message.error(error);
          this.deliveryLoading = false;
        });
    },

    //post添加物流单号
    addLogistic(orderid, logisticsnum) {
      return this.$http
        .post(this.$api.logistic_add, { orderid, logisticsnum })
        .then(res => {
          console.log("res",res)
          if (res.data.result == '1') {
            console.log("res",res)
            this.$Message.success("添加物流单号成功！");
            this.getOrderDetail(orderid);
          } else {
            return Promise.reject("添加物流单号失败！");
          }
        });
    },
    //查看会员详情
    lookMemberDetail() {
      let id = this.orderData.memid;
      if (id) {
        this.$router.push({ path: "/member/detail", query: { id: id } });
      }
    }
  }
};
</script>
<style scoped>
.sec-header {
  background-color: #f1f4f5;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  color: #3bc0c3;
}

.order-sum {
  padding: 10px 20px;
  border: 1px solid #f5f5f5;
  border-top: none;
}

.sum-item {
  text-align: right;
  height: 30px;
  line-height: 30px;
}

.sum-item > span {
  display: inline-block;
}

.sum-item > span:nth-child(1) {
  width: 100px;
  text-align: left;
}

.sum-item > span:nth-child(2) {
  width: 150px;
}

.order-status {
  font-size: 24px;
  margin-bottom: 10px;
  padding: 5px 0;
}

.sec-main {
  min-height: 100px;
  padding: 20px 30px;
}

.order-info-detail {
  line-height: 30px;
}

.order-remark {
  margin: 20px 0;
  background-color: #fff7cc;
}

.order-info-detail span {
  display: inline-block;
  padding: 0 5px;
  height: 30px;
}

.mem-name {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  height: 30px;
  font-size: 24px;
  line-height: 30px;
}

.order-mem-avatar {
  margin-bottom: 10px;
  padding: 5px 0;
}

.order-operate {
  text-align: right;
  padding: 0 30px;
}
</style>



